import React from 'react';
import JSPlayer2 from './playermov'
import "react-multi-carousel/lib/styles.css";
import "./movie.css"
import { Button, Col, Row, Container} from 'react-bootstrap'
import ReactPlayer from 'react-player'
import "video-react/dist/video-react.css";
import Report from '../videofunctions/report.js'
import WatchLater from '../videofunctions/watchlater.js'
import Favorite from '../videofunctions/favorite.js'
import Header from '../header/header'
import { Link } from "react-router-dom";
import Cookies from 'js-cookie'
import axios from 'axios'
import {
    MobileView,
    isMobile, 
    isTablet,
    isBrowser,
    isSmartTV
  } from "react-device-detect";

class MovDetail extends React.Component {
    constructor(props) {
        super();
        const user = Cookies.get('name')
        this.state = {
            movie: [],
            info:[],
            cast:[],
            crew:[],
            poster: '',
            loaded:false,
            itemId: null,
            name: user,
            favorite: [],
            watchLater: [],
            genres: []
        }
        this.togglePlayer = this.togglePlayer.bind(this)
        this.toggleTrailer = this.toggleTrailer.bind(this)



    };

    toggleTrailer = () => {
        this.setState ({ trailer: '1',
                          player: false})


    }
    togglePlayer = () => {
        
            this.setState ({ player: true,
                trailer: '2'   })
                let config = {
                    headers: {
                      'Content-Type': 'application/json',
                      'username': `${this.state.name}`,
                      'id': `${this.state.movie.id}`,
                      'title' :`${this.state.movie.title}`,
                      'poster': `${this.state.movie.poster}`,
                      'background': `${this.state.movie.background}`,
                      'tmdbId': `${this.state.movie.tmdbId}`,
                      'trailer': `${this.state.movie.trailer}`,

                    }
                  }
                  axios
                    .patch(`${process.env.REACT_APP_URL}/api/recent/add`, config)
                    .then(res => {
                      if(res.data.result === "success") {
                        this.setState({
                          recent: 0
                      })
                    }
                 }) 
              }   

componentDidUpdate(prevProps, prevState) {
    if (this.state.watchLater[0] ==! prevState.watchLater[0]){ 
        fetch(`${process.env.REACT_APP_URL}/api/watchlater/get`, {
            headers: {
              'Content-Type': 'application/json',
              'Username': `${this.state.name}`
            },
          })
          .then(res => res.json())
          .then((data)=> {
            this.setState({
                watchLater: data.filter(x => x.tmdbId === this.state.movie.tmdbId)
            })        
        })
     }
     if (this.state.favorite[0] ==! prevState.favorite[0]){ 
        fetch(`${process.env.REACT_APP_URL}/api/favorite/get`, {
            headers: {
              'Content-Type': 'application/json',
              'Username': `${this.state.name}`
            },
          })
          .then(res => res.json())
          .then((data)=> {
            this.setState({
                favorite: data.filter(x => x.tmdbId === this.state.movie.tmdbId)
            })        
        })
     }
        }

    componentDidMount() {
        fetch(`${process.env.REACT_APP_URL}/api/movie/${this.props.match.params.id}`)
        .then(res => res.json())
        .then((data)=> {
            this.setState({ 
                movie: data[0],
                genres: data[0].genres[0]
            })
            return fetch(`https://api.themoviedb.org/3/movie/${this.state.movie.tmdbId}?api_key=${process.env.REACT_APP_TMDB}&append_to_response=credits`)
        })
        .then(res => res.json())
        .then((data) => {
            this.setState({
                info: data,
                cast: data.credits.cast.slice(0,7),
                crew: data.credits.crew.find(c => c.job =='Director')
            })
        })
        
    .catch(console.log)
        fetch(`${process.env.REACT_APP_URL}/api/watchlater/get`, {
            headers: {
              'Content-Type': 'application/json',
              'Username': `${this.state.name}`
            },
          })
          .then(res => res.json())
          .then((data)=> {
              
            this.setState({
                watchLater: data.filter(x => x.tmdbId === this.state.movie.tmdbId).length
            })  
        })
        fetch(`${process.env.REACT_APP_URL}/api/favorite/get`, {
            headers: {
              'Content-Type': 'application/json',
              'Username': `${this.state.name}`
            },
          })
          .then(res => res.json())
          .then((data)=> {
            this.setState({
                favorite: data.filter(x => x.tmdbId === this.state.movie.tmdbId).length
            })  
        })

}

    render() 
    {
        return (
            <>
            <Header />
<Container fluid className="UpperHeader2" style={{backgroundImage:`url(http://image.tmdb.org/t/p/original${this.state.movie.background})`, backgroundSize: 'cover'}}>
    <Row xs={12}>
        <Col lg={4} className="DetailPanel ">
            <img src={`http://image.tmdb.org/t/p/w342${this.state.movie.poster}`} style={{width:"75%"}}alt={`${this.state.movie.title} poster`} /> 
           <Row>
               <Col>
               <Report stream="Movie" location={this.state.movie.title} user={this.state.name} platform={navigator.userAgent} vendor={navigator.vendor}/>
               </Col>
               <Col>
               <Favorite favorite={this.state.favorite} movie={this.state.movie} user={this.state.name} id={this.props.match.params.id}/>
               </Col>
               <Col>
               <WatchLater watchlater={this.state.watchLater} movie={this.state.movie} user={this.state.name} id={this.props.match.params.id}/>
               </Col>
            </Row>
            
            <h2 style={{marginTop:"5%"}}>{this.state.movie.title}</h2>
        <span style={{ fontSize:"12px", color:"#b7b7b7"}}>{this.state.movie.year} | {Math.floor(this.state.info.runtime / 60)}h {this.state.info.runtime - (Math.floor(this.state.info.runtime / 60) * 60)}m</span><br />
        <span>Directed by: <Link to={`/person/${this.state.crew.id}`}>{this.state.crew.name}</Link></span> <br />       
        <MobileView>{ (isMobile || isTablet) && this.state.trailer ==='1' &&
            <ReactPlayer className="player" url={`https://www.youtube.com/watch?v=${this.state.movie.trailer}`}
            playing
            controls={true}
            playsinline={false} 
            width="350px"
            height="196px"
            /> }

            { (isMobile || isTablet) && this.state.trailer ==='2'&& <Row className="player justify-content-center">

            <JSPlayer2 source={`https://lp-server1.lastplay.tv/m${this.state.movie.file}`}
            playsinline={false} 
            controls={true}
            width={350}
            height={196}
            /> </Row>}

        </MobileView>
        <span> {this.state.movie.overview}</span> <br />
        <Button style={{marginTop:"5%"}}variant="danger" onClick={ this.toggleTrailer }><svg width="1.2em" style={{marginBottom:'2px'}} height="1.2em" viewBox="0 0 16 16" class="bi bi-collection-play-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
  <path fill-rule="evenodd" d="M1.5 14.5A1.5 1.5 0 0 1 0 13V6a1.5 1.5 0 0 1 1.5-1.5h13A1.5 1.5 0 0 1 16 6v7a1.5 1.5 0 0 1-1.5 1.5h-13zm5.265-7.924A.5.5 0 0 0 6 7v5a.5.5 0 0 0 .765.424l4-2.5a.5.5 0 0 0 0-.848l-4-2.5zM2 3a.5.5 0 0 0 .5.5h11a.5.5 0 0 0 0-1h-11A.5.5 0 0 0 2 3zm2-2a.5.5 0 0 0 .5.5h7a.5.5 0 0 0 0-1h-7A.5.5 0 0 0 4 1z"/>
</svg> Watch Trailer</Button>{' '}<br />
        <Button style={{marginTop:"5%"}} variant="primary" onClick={ this.togglePlayer }><svg style={{marginBottom:'3px'}} width="1.5em" height="1.5em" viewBox="0 0 16 16" class="bi bi-play-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
  <path d="M11.596 8.697l-6.363 3.692c-.54.313-1.233-.066-1.233-.697V4.308c0-.63.692-1.01 1.233-.696l6.363 3.692a.802.802 0 0 1 0 1.393z"/>
        </svg>Watch Movie</Button>{' '}<br /><br />
<b>Genres</b> <br />
        {this.state.genres.map(cast => {

        return (
<span> {cast.name}</span>
            

        )
})}
        


        </Col>
        


{ (isBrowser || isSmartTV) && this.state.trailer ==='1' &&  <Col className="player">


            <ReactPlayer url={`https://www.youtube.com/watch?v=${this.state.movie.trailer}`}
            playing
            controls={true}
            playsinline={false} 
            width="720px"
            height="405px"
            /> </Col>}


{ (isBrowser || isSmartTV) && this.state.trailer ==='2' &&
<Col className="player">
<JSPlayer2 source={`https://lp-server1.lastplay.tv/m${this.state.movie.file}`}
            playsinline={false} 
            controls={true}
            className="VideoPlayer"
            poster={`http://image.tmdb.org/t/p/w342${this.state.movie.background}`}
            /></Col>
        }

    </Row>

</Container>

<Container><h4>
        Cast
    </h4> 
<Row  >

{this.state.cast.map(cast => {
    let i = 0;
        i++
        return (
            <Col className="cast">
                        <Link to={{
                pathname:`/person/${cast.id}`}} >
                    <img src={`https://image.tmdb.org/t/p/w185${cast.profile_path}`} onError={(e)=>{e.target.onerror = null; e.target.src=`${process.env.PUBLIC_URL}/lpposter.png`}} style={{width:'100px', height:'150px'}} alt="Cast_Poster"/><br />
                    <b><span>{cast.name}</span></b><br />
                     <i>{cast.character}</i>
               
            </Link>
            </Col>
            

        )
})}
    </Row>

</Container>

</>

    
        )
        }
    }
    

export default MovDetail;

