import React, { Component } from 'react';
import { Player, BigPlayButton, ControlBar, ClosedCaptionButton } from 'video-react';
import "video-react/dist/video-react.css";



const sources = ['http://media.w3.org/2010/05/sintel/trailer.mp4', 'http://media.w3.org/2010/05/bunny/trailer.mp4'];

export default class JSPlayer2 extends Component {
  constructor(props, context) {
    super(props, context);

    this.state = {
      source: sources[0]
    };
    this.play = this.play.bind(this);
    this.pause = this.pause.bind(this);
    this.load = this.load.bind(this);
    this.changeCurrentTime = this.changeCurrentTime.bind(this);
    this.seek = this.seek.bind(this);
    this.changePlaybackRateRate = this.changePlaybackRateRate.bind(this);
    this.changeVolume = this.changeVolume.bind(this);
    this.setMuted = this.setMuted.bind(this);
  }


  

  componentDidMount() {
    // subscribe state change
    console.log(sources)
      }

  setMuted(muted) {
    return () => {
      this.player.muted = muted;
    };
  }

  handleStateChange(state) {
    // copy player state to this component's state
    const { player } = this.player.getState();
if(player.ended) {
  this.setState({

    source: sources[1]
  });
  this.player.load();
  this.player.pause();
  this.player.play()

console.log(this.state.player) }

    this.setState({
      player: state,
      source: sources[+1]

    });


  }

  play() {
    this.player.play();


  }

  pause() {
    this.player.pause();
  }

  load() {
    this.player.load();

  }

  changeCurrentTime(seconds) {
    return () => {
      const { player } = this.player.getState();
      this.player.seek(player.currentTime + seconds);
    };
  }

  seek(seconds) {
    return () => {
      this.player.seek(seconds);
    };
  }

  changePlaybackRateRate(steps) {
    return () => {
      const { player } = this.player.getState();
      this.player.playbackRate = player.playbackRate + steps;
    };
  }

  changeVolume(steps) {
    return () => {
      const { player } = this.player.getState();
      this.player.volume = player.volume + steps;
    };
  }

  changeSource(name) {
    return () => {
      this.setState({
        source: sources[name]
      });
      this.player.load();
    };
  }

  render() {
    return (
      <>
      <Player
      crossOrigin="true"
      poster={this.props.poster}
      src={this.props.source}
      className="video-react-big-play-button-center"
      fluid={false}
      width={720}
      height={405}
      autoPlay={true}>
        
              <BigPlayButton position="center" />
              <ControlBar autoHide={false}>
        <ClosedCaptionButton order={7} />
      </ControlBar>
      <track
        kind="captions"
        src={`${this.props.source.slice(0,-3)}es.srt.vtt`}
        srcLang="es"
        label="Spanish"
      />

      
        </Player>


      </>
    );
  }
}