import React from 'react'
import axios from 'axios'
import { Col, Row, Container, InputGroup, FormControl, Button} from 'react-bootstrap'
import { Link } from "react-router-dom";
import swal from "sweetalert";
import Cookies from 'js-cookie'

class Request extends React.Component {

componentDidMount() {
  let user = Cookies.get('name')
  this.setState({ 
    username: user,
})
}
  state = {request: ''}

  handleSubmit = async (event) => {
    event.preventDefault();
      axios
        .post(`${process.env.REACT_APP_URL}/request`, {'request':`${this.state.request}`, 'user': `${this.state.username}`})
        .then(res => {
          if(res.data.result === "success") {
            swal("Thank you!", res.data.message, "success") } else if (res.data.result === "error") {
              swal("Error!", res.data.message, "error");
            }      })
        .catch(error => {
          console.log(error);
        });
  };


	render() {

    return (
      <>
      <Container fluid>
      <Row xs={12} style={{marginTop:"3%"}}className="accordian text-center">
        <Col xs={12} className="requestMovie">
          <h2>MOVIE REQUEST</h2>
          <i>Have something in mind that you want to watch?</i>
            <Row  className="justify-content-center"xs={12} md={2} lg={2} style={{marginTop:'1%'}}>
           <form onSubmit={this.handleSubmit}>
              <InputGroup  size="lg" >
                <FormControl  value={this.state.request} onChange={event => this.setState({request: event.target.value})} placeholder="Enter movie title..." aria-label="Large"  aria-describedby="inputGroup-sizing-sm" />
                <Button onSubmit={this.handleSubmit} variant="secondary" type="submit">
    Submit
  </Button>
              </InputGroup>

              </form>
            </Row>

            <p style={{fontSize:'12px'}}>To search for a movie in our library please use the search button above. For any other inquiry, please <Link to={'/contact'}>click here</Link> to use our contact page.</p>
    </Col></Row>
</Container>
      </>
    )
  }
}

export default Request