import React, { Component } from 'react'
import { Link } from "react-router-dom";
import { Col, Row,Container, Carousel,Button, Image} from 'react-bootstrap'
import '../style.css'
import './style.css'

class Featured extends Component {
    constructor() {
        super()
        this.state = {
            movies: []
        }
    }

componentDidMount() {
    fetch(`${process.env.REACT_APP_URL}/api/movie/featured`)
    .then(res => res.json())
    .then((data)=> {
        this.setState({ 
            movies: data
        })
        console.log(data)
    })
    .catch(console.log)
}


render()  {
{
    return (<div>


            <Row className="popular-header"><h2><svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-bookmark-star-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
  <path fill-rule="evenodd" d="M4 0a2 2 0 0 0-2 2v13.5a.5.5 0 0 0 .74.439L8 13.069l5.26 2.87A.5.5 0 0 0 14 15.5V2a2 2 0 0 0-2-2H4zm4.16 4.1a.178.178 0 0 0-.32 0l-.634 1.285a.178.178 0 0 1-.134.098l-1.42.206a.178.178 0 0 0-.098.303L6.58 6.993c.042.041.061.1.051.158L6.39 8.565a.178.178 0 0 0 .258.187l1.27-.668a.178.178 0 0 1 .165 0l1.27.668a.178.178 0 0 0 .257-.187L9.368 7.15a.178.178 0 0 1 .05-.158l1.028-1.001a.178.178 0 0 0-.098-.303l-1.42-.206a.178.178 0 0 1-.134-.098L8.16 4.1z"/>
</svg> Featured Movies</h2></Row>
<Row className="justify-content-center">
    
{this.state.movies.slice(0,4).map(movie => {
            return (
                <Col xs={6} lg={3} className="popular">

              <div key={`${movie.id}`}>
              <Link  to={{pathname:`/movie/${movie.id}`}}>
                  <img src={`https://image.tmdb.org/t/p/w500${movie.poster}`} style={{width:'100%'}} alt={`${movie.title} poster`} />
              </Link>
          </div>
          </Col>
            )
         

   })
}

</Row>


</div>    ) }  }}

export default Featured
